.range {
    height: 10px;
    -webkit-appearance: none;
    margin: 10px 0;
}

/*Бегунок*/
.range::-webkit-slider-thumb {
    box-shadow: 2px 2px 1px rgba(53, 53, 53, 0.25);
    border: 1px solid #353535;
    height: 20px;
    width: 5px;
    border-radius: 15px;
    background: #ffffff;
    cursor: pointer;
    -webkit-appearance: none;
    margin-top: -6px;
}

/*ренж*/
.range::-webkit-slider-runnable-track {
    width: 100%;
    height: 10px;
    cursor: pointer;
    background: #529cf6;
    border-radius: 5px;
    border: 1px solid #353535;
}